import { Component, DoCheck ,OnInit } from '@angular/core';
import { UserService } from '../../services/user.service';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { PrendaService } from '../../services/prendas.service';
import { GLOBAL } from '../../services/global';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css'],
  providers: [UserService,PrendaService]
})
export class NavbarComponent implements OnInit{
  public identity;
  public user;
  public url: string;
  public prendas: any[];
  public cantidad = 0;
  constructor(private _userService: UserService,private _route: ActivatedRoute,private _router:Router,private _prendaService: PrendaService){
    this.url = GLOBAL.url;
    this.user = this.identity;
  }
  ngOnInit(){
    this.identity = this._userService.getIdentity();
  }
  ngDoCheck(){
    this.identity = this._userService.getIdentity();
    this.prendas = this._prendaService.getCar();
    if(!this.prendas){
        this.cantidad = 0;
    }else{
      this.cantidad = this.prendas.length; 
    }
    
  }
  logout(){
    localStorage.clear();
    this.identity= null;
    this._router.navigate(['/']);
  }
  carrito(){
    this.prendas = this._prendaService.prendaElegida;
    return this.prendas;
  }  
  eliminarPrenda(elemento){
    this._prendaService.borrarPrenda(elemento);
  }
}



