import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';
import { Observable } from 'rxjs/Observable';
import { GLOBAL } from './global';

@Injectable()
export class PrendaService{
    public url:string;
    public prendaElegida: any[]=[];
    
    constructor(private _http: Http){
        this.url = GLOBAL.url;
    }
    addPrenda(token, prenda, tipo){
        let params = JSON.stringify(prenda);
        let headers = new Headers({
            'Content-Type' : 'application/json',
            'Authorization' : token
        });
        return this._http.post(this.url+tipo, params, {headers: headers})
                          .map(res => res.json());
    }





    getPrendasHombre(){
        return this._http.get(this.url+'prenda-hombre').map(res => res.json());
    }
    getPrendasNino(){
        return this._http.get(this.url+'prenda-nino').map(res => res.json());
    }
    getPrendasMujer(){
        return this._http.get(this.url+'prenda-mujer').map(res => res.json());
    }
    car(prenda){
        console.log(this.prendaElegida);
        this.prendaElegida.push(prenda);
        localStorage.setItem('prendas',JSON.stringify(this.prendaElegida));
        console.log(this.prendaElegida);
        return this.prendaElegida;
    }
    getCar(){
        let prendas = JSON.parse(localStorage.getItem('prendas'));
        if(prendas != "undefined"){
            this.prendaElegida = prendas;
        }else{
            this.prendaElegida = [];
        }
        return this.prendaElegida;
    }
    borrarPrenda(elemento){
        this.prendaElegida.splice(elemento,1);
        localStorage.setItem('prendas',JSON.stringify(this.prendaElegida));
        console.log(this.prendaElegida);
        return this.prendaElegida;   
    }
}


