import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { GLOBAL} from '../../services/global';
import { PrendaService } from '../../services/prendas.service';
import { Hombre } from '../../models/hombre';
import { fundido } from '../../animation';

@Component({
  selector: 'app-hombre',
  templateUrl: './hombre.component.html',
  styleUrls: ['./hombre.component.css'],
  animations:[fundido],
  providers: [PrendaService]
})
export class HombreComponent implements OnInit {
  public url;
  public prendas: Hombre[];  
  
 constructor(
   private _route: ActivatedRoute,
   private _router: Router,
   private _prendaService: PrendaService
 ) {
   this.url = GLOBAL.url;
  }

 ngOnInit() {
   this._prendaService.getPrendasHombre().subscribe(
     response =>{
       if(!response.prendas){

       }else{
         this.prendas = response.prendas;
       }
     },
     error => {
       console.log(<any>error);
     }
   );
 }
 car(prenda){
   this._prendaService.car(prenda);
   alert('Prenda añadida con exito');
 }
 
}



