import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { GLOBAL} from '../../services/global';
import { PrendaService } from '../../services/prendas.service';
import { Nino } from '../../models/nino';
import { fundido } from '../../animation';


@Component({
  selector: 'app-ninos',
  templateUrl: './ninos.component.html',
  styleUrls: ['./ninos.component.css'],
  animations:[fundido],  
  providers: [PrendaService]
})
export class NinosComponent implements OnInit {
  public url;
  public prendas: Nino[];  
 constructor(
   private _route: ActivatedRoute,
   private _router: Router,
   private _prendaService: PrendaService
 ) {
   this.url = GLOBAL.url;
  }

 ngOnInit() {
   this._prendaService.getPrendasNino().subscribe(
     response =>{
       if(!response.prendas){

       }else{
         this.prendas = response.prendas;
       }
     },
     error => {
       console.log(<any>error);
     }
   );
 }
 
}



