import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { GLOBAL} from '../../services/global';
import { PrendaService } from '../../services/prendas.service';
import { Mujer } from '../../models/Mujer';
import { fundido } from '../../animation';

@Component({
  selector: 'app-mujer',
  templateUrl: './mujer.component.html',
  styleUrls: ['./mujer.component.css'],
  animations:[fundido],
  providers: [PrendaService]
})
export class MujerComponent implements OnInit {

  public url;
  public prendas: Mujer[];  
 constructor(
   private _route: ActivatedRoute,
   private _router: Router,
   private _prendaService: PrendaService
 ) {
   this.url = GLOBAL.url;
  }

 ngOnInit() {
   this._prendaService.getPrendasMujer().subscribe(
     response =>{
       if(!response.prendas){

       }else{
         this.prendas = response.prendas;
       }
     },
     error => {
       console.log(<any>error);
     }
   );
 }
 
}



